import { takeLatest, put, call, fork, all } from 'redux-saga/effects';
import { MODAL_SUCCESS } from '../modules/modalState';
import { REGISTER, REGISTER_SUCCESS, REGISTER_FAILURE } from '../modules/register';
import { customAxios } from '../util/axios';
//   
//https://kyounghwan01.github.io/blog/React/redux/redux-saga/#react%E1%84%8B%E1%85%A6%E1%84%89%E1%85%A5-saga-%E1%84%89%E1%85%A1%E1%84%8B%E1%85%AD%E1%86%BC%E1%84%92%E1%85%A1%E1%84%80%E1%85%B5
//https://jeonghwan-kim.github.io/dev/2019/07/22/react-saga-ts-1.html

// id password     
const registerApi = async (id: string, password: string, nickname: string): Promise<any> => {
  return customAxios('post', '/register/join', { id, password, nickname })
    .then((res) => {
      console.log(res.data);

      return res.data;
    })
    .catch((err) => console.log(err));

  // return await axios.post("http://localhost:1234/register/join",{id,password})
  // .then(res => {
  //     //  number  return
  //     console.log(res.data) // 200
  //     return res.data
  // })
};

//         
//  dispatch   payload .
//   registerApi    action.payload.id     .
function* registerApi$(action: any): Generator<any, any, any> {
  try {
    console.log(action);
    //api   .
    const memos = yield call(registerApi, action.payload.id, action.payload.password, action.payload.nickname);
    console.log(memos); // api   
    //put dispatch  .
    //REGISTER  REGISTER_SUCCESS     .
    if (memos.code === 404) yield put({ type: REGISTER_FAILURE, payload: memos });
    if (memos.code === 200) {
      yield put({ type: REGISTER_SUCCESS, payload: memos });
      yield put({ type: MODAL_SUCCESS });
    }
  } catch (err) {
    console.log(err);
  }
}

function* getRegisterApi() {
  yield takeLatest(REGISTER, registerApi$);
}

export default function* getRegisterApiSaga() {
  yield all([fork(getRegisterApi)]);
}
