import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
// import reportWebVitals from './reportWebVitals';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import { Provider } from 'react-redux';
import { createStore, applyMiddleware } from 'redux';
import rootReducer from './modules/modules_index';
import createSagaMiddleware from '@redux-saga/core';
import rootSaga from './saga/root_saga';
import Notfound from './page/Notfound';
import Home from './page/Home';
import { login_localstorage } from './modules/login';
import Layout from './layout/layout';
import Dungeon from './page/Dungeon';
import Rangking from './page/Ranking';
import LayoutNotInfo from './layout/layoutNotInfo';
import axios from 'axios';
import MyStatus from './page/MyStatus';
import Shop from './page/Shop';
import NewDungeonFight from './page/NewDungeonFight';
import NewPenGame from './page/NewPenGame';
import NewRegister from './page/NewRegister';
// 
const sagaMiddleware = createSagaMiddleware();
//   .
const store = createStore(rootReducer, applyMiddleware(sagaMiddleware));

// run rootSaga 
sagaMiddleware.run(rootSaga);

//   ,      ,    return
function loadUser() {
  try {
    let user: string | null = localStorage.getItem('token');
    if (!user) return;
    axios.defaults.headers.common['Authorization'] = user;
    store.dispatch(login_localstorage(user));
  } catch (e) {
    console.log(e);
  }
}
loadUser();

const root = ReactDOM.createRoot(document.getElementById('root') as HTMLElement);
root.render(
  <Provider store={store}>
    <BrowserRouter>
      <Routes>
        <Route element={<Layout />}>
          <Route
            path='/'
            element={<App />}
          ></Route>
          <Route
            path='/register'
            element={<NewRegister />}
          ></Route>
          <Route
            path='/home'
            element={<Home />}
          ></Route>
          <Route
            path='/status'
            element={<MyStatus />}
          ></Route>
          <Route
            path='*'
            element={<Notfound />}
          ></Route>
          <Route
            path='/playpengame'
            element={<NewPenGame />}
          ></Route>
          <Route
            path='/dungeon'
            element={<Dungeon />}
          ></Route>

          <Route
            path='/dungeonfight'
            element={<NewDungeonFight />}
          ></Route>
          <Route
            path='/dungeonfightbefore'
            element={<NewDungeonFight />}
          ></Route>
          <Route
            path='/ballpenshop'
            element={<Shop />}
          ></Route>
        </Route>
        <Route element={<LayoutNotInfo />}>
          <Route
            path='/ranking'
            element={<Rangking />}
          ></Route>
        </Route>
      </Routes>
    </BrowserRouter>
  </Provider>,
);

