import { takeLatest, fork, all, put, call, delay } from 'redux-saga/effects';
import {
  LOGIN_REQUEST,
  LOGIN_SUCCESS,
  LOGIN_LOCALSTORAGE,
  LOGIN_LOCALSTORAGE_SUCCESS,
  LOGIN_LOCALSTORAGE_FAILURE,
  LOGOUT_REQUSET,
} from '../modules/login';
import customAxios from '../util/axios';
import axios from 'axios';
import { UPDATE_BALLPEN_SUCCESS } from '../modules/buyBallpenList';
import { UPDATE_PAPER_SUCCESS } from '../modules/buyPaperList';
import { ISLOADING_SUCCESS, ISLOADING_FAILURE, ISLOADING_REQUEST } from '../modules/isLoding';
const updateBallPenListApi = async (): Promise<boolean> => {
  return await customAxios('post', '/shop/updateballpen').then((res) => {
    return res.data;
  });
};
const updatePaperListApi = async (): Promise<boolean> => {
  return await customAxios('post', '/shop/updatepaper').then((res) => {
    return res.data;
  });
};

const loginApi = async (id: string, password: string): Promise<any> => {
  return await customAxios('post', '/login/', { id, password })
    .then((res) => {
      //      axios  
      if (res.data.code === 200) {
        localStorage.setItem('token', res.data.token);

        axios.defaults.headers.common['Authorization'] = res.data.token;
      }
      return res.data;
    })
    .catch((err) => {
      console.log(err);
    });
};

function* loginApi$(action: any): Generator<any, any, any> {
  try {
    const result = yield call(loginApi, action.payload.id, action.payload.password);
    yield put({ type: ISLOADING_REQUEST });
    yield delay(400);

    if (result.code === 200) {
      yield put({ type: ISLOADING_SUCCESS });
      yield delay(600);

      yield put({
        type: LOGIN_SUCCESS,
        token: result.token,
        id: result.id,
        userInfo: result.userInfo,
      });
      //   
      const penList = yield call(updateBallPenListApi);
      //  
      const paperList = yield call(updatePaperListApi);
      //    .
      yield put({ type: UPDATE_BALLPEN_SUCCESS, buyBallpenList: penList.buyBallpenList });
      yield put({ type: UPDATE_PAPER_SUCCESS, buyPaperList: paperList.buyPaperList });
    }
    if (result.code === 201) yield put({ type: LOGIN_SUCCESS, token: result.token, id: result.id });
    if (result.code === 404) {
      yield put({ type: ISLOADING_FAILURE, text: result.message });
    }
    if (result.code === 405) {
      yield put({ type: ISLOADING_FAILURE, text: result.message });
    }
  } catch (err) {
    console.log(err);
  }
}

const loginLocalStorage = async (token: any): Promise<any> => {
  return await customAxios('post', '/login/localstorage', { token }).then((res) => {
    axios.defaults.headers.common['Authorization'] = res.data.token;
    // if(res.data.code === 200){
    //     console.log("200")
    //     axios.defaults.headers.common[
    //         'Authorization'
    //       ] = `Bearer ${res.data.token}`; // api  
    // }
    return res.data;
  });
};

function* loginLocalStorage$(action: any): Generator<any, any, any> {
  try {
    const result = yield call(loginLocalStorage, action.token);

    if (result.code === 200) {
      yield put({
        type: LOGIN_LOCALSTORAGE_SUCCESS,
        token: result.token,
        id: result.id.userId,
        userInfo: result.userInfo,
      });
      //DB   .
      const penList = yield call(updateBallPenListApi);
      //  
      const paperList = yield call(updatePaperListApi);

      //   .
      yield put({ type: UPDATE_BALLPEN_SUCCESS, buyBallpenList: penList.buyBallpenList });
      yield put({ type: UPDATE_PAPER_SUCCESS, buyPaperList: paperList.buyPaperList });
    }
    if (result.code === 201)
      yield put({
        type: LOGIN_LOCALSTORAGE_FAILURE,
        token: result.token,
        id: result.id.userId,
        userInfo: result.userInfo,
      });
  } catch (e: any) {
    yield put({ type: LOGIN_LOCALSTORAGE_FAILURE });
    console.log(e);
  }
}

function logout() {
  window.location.replace('/');
  localStorage.removeItem('token');
}

function* getLogout() {
  yield takeLatest(LOGOUT_REQUSET, logout);
}

function* getLoginLocalStorage() {
  yield takeLatest(LOGIN_LOCALSTORAGE, loginLocalStorage$);
}

function* getLoginApi() {
  yield takeLatest(LOGIN_REQUEST, loginApi$);
}

export default function* getLoginApiSage() {
  yield all([fork(getLoginApi), fork(getLoginLocalStorage), fork(getLogout)]);
}
