import React from 'react';
import styled from 'styled-components';
import { useSelector } from 'react-redux';
import { RootState } from '../modules/modules_index';
import { LoginUserInfoInterface } from '../modules/login';
import { expTable } from '../util/expTable';

const StatLists = styled.li`
  display: flex;
  font-size: 1.5rem;
  margin-bottom: 1px;
`;

export const StatName = styled.span`
  width: 50%;
  height: 35px;
  padding: 5px 0 5px 10px;
  background: #eee;
  /* border-bottom: 1px solid#000; */
  display: flex;
  align-items: center;
  font-weight: bold;
`;

export const StatValue = styled.span`
  width: 50%;
  height: 35px;
  padding: 5px 10px 5px 0;
  background: #333;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: flex-end;
`;

const StatList = () => {
  const userInfo: LoginUserInfoInterface = useSelector(
    (state: RootState) => state.login.userInfo,
  ) as LoginUserInfoInterface;

  const statArr = [
    { statName: '', statValue: userInfo.Nickname },
    { statName: '', statValue: userInfo.Level },
    {
      statName: '',
      statValue: userInfo.Exp.toLocaleString() + ' / ' + expTable[userInfo.Level].toLocaleString(),
    },
    { statName: '', statValue: userInfo.BasicHp.toLocaleString() },
    { statName: '', statValue: (userInfo.BasicHp + userInfo.WeaponDamage).toLocaleString() },
    { statName: ' ', statValue: userInfo.RevivalPoint },
    { statName: ' ', statValue: userInfo.Gold.toLocaleString() },
    { statName: '  ', statValue: userInfo.MaxDungeonFloor },
    { statName: '  ', statValue: userInfo.PenCount },
    { statName: '  ', statValue: userInfo.DungeonClearCount },
    { statName: ' ', statValue: userInfo.RevivalCount },
  ];

  return (
    <>
      {statArr.map((i: any, index: number) => {
        return (
          <StatLists key={index}>
            <StatName>{i.statName}</StatName>
            <StatValue>{i.statValue}</StatValue>
          </StatLists>
        );
      })}
    </>
  );
};

export default React.memo(StatList);
